import {useState, useRef, useEffect} from 'react';

import {useNavigate} from 'react-router-dom';
import {useSelector} from 'react-redux';
import {getSavedPublicAddress, isLoggedIn, login, logout} from 'utils/Auth';
import {getLastTransactionLabel, reload, maskAddress} from 'utils/AppUtils';

// material-ui
import {useTheme} from '@mui/material/styles';
import {
    Avatar,
    Box,
    Card,
    CardContent,
    Chip,
    ClickAwayListener,
    Divider,
    Grid,
    InputAdornment,
    List,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    OutlinedInput,
    Paper,
    Popper,
    Stack,
    Switch,
    Typography
} from '@mui/material';

// third-party
import PerfectScrollbar from 'react-perfect-scrollbar';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import Transitions from 'ui-component/extended/Transitions';
import UpgradePlanCard from './UpgradePlanCard';
import MetaMask from 'assets/images/icons/metamask.png';

// assets
import {IconLogout, IconSearch, IconSettings, IconUser} from '@tabler/icons';

import {Web3Service} from 'utils/Web3Service';
import {Service} from 'utils/Service';
import web3 from 'web3';

// ==============================|| PROFILE MENU ||============================== //

const ProfileSection = () => {
    const theme = useTheme();
    const customization = useSelector((state) => state.customization);
    const navigate = useNavigate();

    const [sdm, setSdm] = useState(true);
    const [value, setValue] = useState('');
    const [notification, setNotification] = useState(false);
    const [selectedIndex, setSelectedIndex] = useState(-1);
    const [open, setOpen] = useState(false);
    const [savedPublicAddress, setSavedPublicAddress] = useState('');
    const [label, setLabel] = useState('');
    const [lastTransaction, setLastTransaction] = useState(null);

    const [khoBalance, setKhoBalance] = useState('-');
    const [consumableBalance, setConsumableBalance] = useState('-');
    /**
     * anchorRef is used on different componets and specifying one type leads to other components throwing an error
     * */
    const anchorRef = useRef(null);
    const handleLogout = async () => {
        logout();
        reload();
    };

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }
        setOpen(false);
    };

    const handleListItemClick = (event, index, route = '') => {
        setSelectedIndex(index);
        handleClose(event);

        if (route && route !== '') {
            navigate(route);
        }
    };
    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const loginWithMetaMask = async () => {
        const web3Service = await Web3Service.getInstance();
        const publicAddress = await web3Service.userAddress();
        Service.userGetNonce(publicAddress)
            // Popup MetaMask confirmation modal to sign message
            .then((data) => web3Service.loginSignature(publicAddress, data.nonce))
            // Send signature to backend on the /auth route
            .then((signature) => Service.userCreateAccessToken(publicAddress, signature))
            // Pass accessToken back to parent component (to save it in localStorage)
            .then(handleLoggedIn)
            .catch((err) => {
                console.error(err);
                window.alert(err);
            });
    };

    const handleLoggedIn = (auth) => {
        login(auth);
        reload();
    };

    const prevOpen = useRef(open);
    useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current.focus();
        }

        prevOpen.current = open;

        if (isLoggedIn()) {
            const address = getSavedPublicAddress();
            setSavedPublicAddress();
            setLabel(`Connected with ${maskAddress(address)}`);

            fetchTokenBalance();
            fetchConsumableBalance();
            fetchTransactions();
        }
    }, [open]);

    const fetchTokenBalance = () => {
        Service.getWalletBalance()
            .then((data) => data.tokenBalance.toString())
            .then((balance) => {
                const amountFromWei = web3.utils.fromWei(balance);
                const amountInFloat = parseFloat(amountFromWei);
                const roundOffBalance = amountInFloat.toFixed(2);
                setKhoBalance(roundOffBalance);
            })
            .catch(console.error);
    };

    const fetchConsumableBalance = () => {
        Service.getConsumableBalance()
            .then((data) => {
                setConsumableBalance(data.amount);
            })
            .catch(console.error);
    };

    const fetchTransactions = () => {
        Service.getTransactions()
            .then((data) => {
                if (data.length > 0) {
                    setLastTransaction(data[0]);
                }
            })
            .catch(console.error);
    };

    const handleSignOnBonusClaimClicked = () => {
        console.log('Free bonus clicked');
    };

    const handleBuyGameTicked = () => {
        console.log('Buy Game Tickets clicked');
    };

    const onLastTransactionClicked = () => {
        window.open('https://ropsten.etherscan.io/tx/' + lastTransaction.txnAddress, '_blank');
    };

    return (
        <>
            {!isLoggedIn() ? (
                <div></div>
            ) : (
                // <Chip
                //     sx={{
                //         height: '40px',
                //         alignItems: 'center',
                //         borderRadius: '27px',
                //         transition: 'all .2s ease-in-out'
                //     }}
                //     icon={
                //         <Avatar
                //             src={MetaMask}
                //             sx={{
                //                 ...theme.typography.mediumAvatar,
                //                 margin: '8px 0 8px 8px !important',
                //                 cursor: 'pointer'
                //             }}
                //             ref={anchorRef}
                //             aria-controls={open ? 'menu-list-grow' : undefined}
                //             aria-haspopup="true"
                //             color="inherit"
                //         />
                //     }
                //     label="Login With MetaMask"
                //     variant="contained"
                //     ref={anchorRef}
                //     aria-controls={open ? 'menu-list-grow' : undefined}
                //     aria-haspopup="true"
                //     onClick={loginWithMetaMask}
                //     color="primary"
                // />
                <div>
                    <Chip
                        sx={{
                            height: '40px',
                            alignItems: 'center',
                            borderRadius: '27px',
                            transition: 'all .2s ease-in-out'
                        }}
                        icon={
                            <Avatar
                                src={MetaMask}
                                sx={{
                                    ...theme.typography.mediumAvatar,
                                    margin: '8px 0 8px 8px !important',
                                    cursor: 'pointer'
                                }}
                                ref={anchorRef}
                                aria-controls={open ? 'menu-list-grow' : undefined}
                                aria-haspopup="true"
                                color="inherit"
                            />
                        }
                        label={label}
                        variant="contained"
                        ref={anchorRef}
                        aria-controls={open ? 'menu-list-grow' : undefined}
                        aria-haspopup="true"
                        onClick={handleToggle}
                        color="primary"
                    />
                    <Popper
                        placement="bottom-end"
                        open={open}
                        anchorEl={anchorRef.current}
                        role={undefined}
                        transition
                        disablePortal
                        popperOptions={{
                            modifiers: [
                                {
                                    name: 'offset',
                                    options: {
                                        offset: [0, 14]
                                    }
                                }
                            ]
                        }}
                    >
                        {({TransitionProps}) => (
                            <Transitions in={open} {...TransitionProps}>
                                <Paper>
                                    <ClickAwayListener onClickAway={handleClose}>
                                        <MainCard border={false} elevation={16} content={false} boxShadow shadow={theme.shadows[16]}>
                                            <Box sx={{p: 2}}>
                                                <Stack>
                                                    <Stack direction="row" spacing={0.5} alignItems="center">
                                                        <Typography component="span" variant="h4">
                                                            {savedPublicAddress}
                                                        </Typography>
                                                    </Stack>
                                                </Stack>
                                                <Divider />
                                            </Box>
                                            <PerfectScrollbar
                                                style={{
                                                    height: '100%',
                                                    maxHeight: 'calc(100vh - 250px)',
                                                    overflowX: 'hidden'
                                                }}
                                            >
                                                <Box sx={{paddingLeft: 2, paddingRight: 2, paddingBottom: 2, paddingTop: 0}}>
                                                    <UpgradePlanCard
                                                        title={'Claim your reward'}
                                                        subheading={'Transfer $KHO tokens to your wallet.'}
                                                        btnText={'Transfer to my wallet'}
                                                        btnAction={handleSignOnBonusClaimClicked}
                                                    />
                                                    <Divider />
                                                    <UpgradePlanCard
                                                        title={'Buy Game Tickets'}
                                                        subheading={'Buy Game tickets in exchange for KHO tokens'}
                                                        btnText={'Buy Game Tickets'}
                                                        btnAction={handleBuyGameTicked}
                                                    />
                                                    <Divider />
                                                    <Card
                                                        sx={{
                                                            bgcolor: theme.palette.primary.dark,
                                                            my: 2
                                                        }}
                                                    >
                                                        <CardContent>
                                                            <Grid container direction="column">
                                                                <Grid item>
                                                                    <Grid item container alignItems="center" justifyContent="space-between">
                                                                        <Grid item>
                                                                            <Typography variant="subtitle1">$KHO balance</Typography>
                                                                        </Grid>
                                                                        <Grid item>
                                                                            <p>{khoBalance}</p>
                                                                        </Grid>
                                                                    </Grid>
                                                                </Grid>
                                                                <Grid item>
                                                                    <Grid item container alignItems="center" justifyContent="space-between">
                                                                        <Grid item>
                                                                            <Typography variant="subtitle1">$Consumable balance</Typography>
                                                                        </Grid>
                                                                        <Grid item>
                                                                            <p>{consumableBalance}</p>
                                                                        </Grid>
                                                                    </Grid>
                                                                </Grid>
                                                            </Grid>
                                                        </CardContent>
                                                    </Card>
                                                    <Divider />

                                                    {lastTransaction && (
                                                        <div>
                                                            <Card
                                                                sx={{
                                                                    bgcolor: theme.palette.primary.dark,
                                                                    my: 2
                                                                }}
                                                            >
                                                                <CardContent>
                                                                    <Grid container direction="column" spacing={1}>
                                                                        <Grid item>
                                                                            <Grid
                                                                                item
                                                                                container
                                                                                alignItems="center"
                                                                                justifyContent="space-between"
                                                                            >
                                                                                <Grid item>
                                                                                    <Typography variant="subtitle1">
                                                                                        Last transaction of $KHO.
                                                                                    </Typography>
                                                                                </Grid>
                                                                            </Grid>
                                                                        </Grid>
                                                                        <Grid item>
                                                                            <Grid
                                                                                item
                                                                                container
                                                                                alignItems="center"
                                                                                justifyContent="space-between"
                                                                            >
                                                                                <Grid item>
                                                                                    <Typography variant="subtitle1">Amount</Typography>
                                                                                    <Typography variant="subtitle2" sx={{color: 'black'}}>
                                                                                        {getLastTransactionLabel(lastTransaction)}
                                                                                    </Typography>
                                                                                </Grid>
                                                                                <Grid item>
                                                                                    <p>{lastTransaction.amount.toString()}</p>
                                                                                </Grid>
                                                                            </Grid>
                                                                        </Grid>
                                                                        <Grid item>
                                                                            <Grid
                                                                                item
                                                                                container
                                                                                alignItems="center"
                                                                                justifyContent="space-between"
                                                                            >
                                                                                <Grid item>
                                                                                    <Typography variant="subtitle1">Tx address</Typography>
                                                                                    {/*<Typography variant="subtitle2">3 min*/}
                                                                                    {/*    ago.</Typography>*/}
                                                                                </Grid>
                                                                                <Grid item>
                                                                                    <a
                                                                                        href={`https://ropsten.etherscan.io/tx/${lastTransaction.txnAddress}`}
                                                                                        target="_blank"
                                                                                    >
                                                                                        {maskAddress(lastTransaction.txnAddress)}
                                                                                    </a>
                                                                                </Grid>
                                                                            </Grid>
                                                                        </Grid>
                                                                    </Grid>
                                                                </CardContent>
                                                            </Card>
                                                            <Divider />
                                                        </div>
                                                    )}

                                                    <List
                                                        component="nav"
                                                        sx={{
                                                            width: '100%',
                                                            maxWidth: 350,
                                                            minWidth: 300,
                                                            backgroundColor: theme.palette.background.paper,
                                                            borderRadius: '10px',
                                                            [theme.breakpoints.down('md')]: {
                                                                minWidth: '100%'
                                                            },
                                                            '& .MuiListItemButton-root': {
                                                                mt: 0.5
                                                            }
                                                        }}
                                                    >
                                                        <ListItemButton
                                                            sx={{borderRadius: `${customization.borderRadius}px`}}
                                                            selected={selectedIndex === 4}
                                                            onClick={handleLogout}
                                                        >
                                                            <ListItemIcon>
                                                                <IconLogout stroke={1.5} size="1.3rem" />
                                                            </ListItemIcon>
                                                            <ListItemText primary={<Typography variant="body2">Logout</Typography>} />
                                                        </ListItemButton>
                                                    </List>
                                                </Box>
                                            </PerfectScrollbar>
                                        </MainCard>
                                    </ClickAwayListener>
                                </Paper>
                            </Transitions>
                        )}
                    </Popper>
                </div>
            )}
        </>
    );
};

export default ProfileSection;
