import {getSavedPublicAddress} from "./Auth";

export const reload = () => {
    window.location.reload()
};

export const getLastTransactionLabel = (lastTransaction) => {
    const tokenAddress = process.env.REACT_APP_TOKEN_ADDRESS
    const userAddress = getSavedPublicAddress()

    if(!tokenAddress){
        throw new Error("Please set REACT_APP_TOKEN_ADDRESS");
    }

    if(lastTransaction.sender.toLowerCase()===tokenAddress.toLowerCase()
        && lastTransaction.recevier.toLowerCase() === userAddress.toLowerCase()){
        return "From $KHO to your address"
    }
    else if(lastTransaction.sender.toLowerCase()===userAddress.toLowerCase()
        && lastTransaction.recevier.toLowerCase() === tokenAddress.toLowerCase()){
        return "From your address to $KHO"
    }
    else{
        return ""
    }
}

export const truncate = (str, n) => {
    return (str.length > n) ? str.substr(0, n-1) + '...' : str;
};

export const maskAddress = (str) => {
    const length = str.length;
    return str.substr(0, 5) + '...' + str.substr(length-3, length);
}